import { Fragment, useRef, useState } from "react";
import Modal from "../components/UI/modal";
import Spinner from "../components/UI/spinner";

import classes from "./contactMe.module.css"
import { apiRootURL } from "../apiConfigs";
import useInput from "../hooks/useInput";

const ContactMe = () => {
    const [ loading, setLoading ] = useState(false)
    const [ error, setError ] = useState("")
    const [ responseMessage, setresponseMessage] = useState(null);
    const name = useRef()
    const email = useRef()
    const subject = useRef()
    const content = useRef()
    let formValidity = false

    const { 
        value:enterdName,
        isValid:enterdNameIsValid,
        valueChangeHandler:nameChangeHandler,
        inputBlurHandler:nameBlurHandler,
        hasError:nameInputHasError,
        reset:restNameInput
    } = useInput((value)=>{return value.trim() !== ""})

    const { 
        value:enterdEmail,
        isValid:enterdEmailIsValid,
        valueChangeHandler:emailChangeHandler,
        inputBlurHandler:emailBlurHandler,
        hasError:emailInputHasError,
        reset:restEmailInput
    } = useInput((email) => {return String(email).toLowerCase().match(/^(([^<>()[\]\\.,;:\s@"]+(\.[^<>()[\]\\.,;:\s@"]+)*)|(".+"))@((\[[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}\])|(([a-zA-Z\-0-9]+\.)+[a-zA-Z]{2,}))$/)})

    const { 
        value:enterdSubject,
        isValid:enterdSubjectIsValid,
        valueChangeHandler:subjectChangeHandler,
        inputBlurHandler:subjectBlurHandler,
        hasError:subjectInputHasError,
        reset:restSubjectInput
    } = useInput((value)=>{return value.trim() !== ""})

    const { 
        value:enterdMessage,
        isValid:enterdMessageIsValid,
        valueChangeHandler:messageChangeHandler,
        inputBlurHandler:messageBlurHandler,
        hasError:messageInputHasError,
        reset:restMessageInput
    } = useInput((value)=>{return value.trim() !== ""})

    formValidity = enterdNameIsValid && enterdEmailIsValid && enterdSubjectIsValid && enterdMessageIsValid
    
    const contactMeHandler = (event) => {
        event.preventDefault()
        if(!formValidity){
            return
        }
        setLoading(true)

        const messageDetails = {
            name:name.current.value,
            email:email.current.value,
            subject:subject.current.value,
            content:content.current.value
        }

        const URL = apiRootURL + "public/message"
        const METHOD = "POST"

        let errorExists = false
        fetch(URL, {
            method:METHOD,
            body:JSON.stringify(messageDetails),
            headers: {
                "content-type":"application/json"
            }
        }).then((res)=>{
            if (!res.ok) {
                errorExists = true;
              }
              return res.json();
        }).then(res=>{
            if (errorExists) {
                const newError = new Error("Error: " + res.message);
                throw newError;
            }
                setresponseMessage(res.message);
                restNameInput();
                restEmailInput();
                restSubjectInput();
                restMessageInput();
                setLoading(false);
        }).catch(err => {
            setLoading(false);
            setError(err.message);
        })
    }

    const closeModal = () => {
        setError(false)
        setLoading(false)
        setresponseMessage(null)
    }

    return (
        <Fragment>
            <div className="contact-me-page">
                <div className={classes["contact-me"]}>
                    <div className={classes["container"]}>
                        <div className={classes.title}>Contact</div>
                        <div className={classes.details}>
                            <div className={classes.greeting}>
                                There is nothing like having a face to face talk. I build brands people trust. My values and ideas bring people and brands together.
                                <span className={classes.location} onClick={()=>{window.open("https://www.google.com/maps/place/R%C3%BCttenscheid,+Essen/@51.4310205,6.9959507,15z/data=!3m1!4b1!4m5!3m4!1s0x47b8c2d407e67ee7:0x438f5d6840bd9f3!8m2!3d51.4296308!4d7.0039007","_blank")}}>Open in google Maps</span>
                            </div>
                            <div className={classes["contact-details"]}>
                                <div className={classes["address"]}>RÃ¼ttenscheid 45130, Essen, Deutschland</div>
                                <div className={classes["number"]}>+49 175 2936231</div>
                                <div onClick={e=>{ e.preventDefault();window.location.href = "mailto:Hazemseragdev@gmail.com"; }} className={classes["email"]}>Hazemseragdev@gmail.com</div>
                                <div className={classes["icons"]}></div>

                            </div>
                        </div>
                    </div>
                </div>

                <div className={classes["contact-me-form"]}>
                    <div className="title">Let's start!</div>
                    <div className={classes["subtitle"]}>It is really interesting to work with you.</div>
                    <div className={classes["form-container"]}>
                        <form onSubmit={contactMeHandler}>
                            <div className={classes["form-row"]}>
                                <div className={`${classes["form-input"]} ${classes["half"]}`}>
                                    <input ref={name} className={nameInputHasError?"invalid-input":""} onChange={nameChangeHandler} onBlur={nameBlurHandler} value={enterdName} type="text" placeholder="NAME"></input>
                                    {nameInputHasError && <p className={"error-text"}>Please enter a valid name.</p>}
                                </div>
                                <div className={`${classes["form-input"]} ${classes["half"]}`}>
                                    <input ref={email} className={emailInputHasError?"invalid-input":""} onChange={emailChangeHandler} onBlur={emailBlurHandler} value={enterdEmail} type="email" placeholder="EMAIL"></input>
                                    {emailInputHasError && <p className={"error-text"}>Please enter a valid Email.</p>}

                                </div>
                            </div>
                            <div className={classes["form-input"]}>
                                <input ref={subject} className={subjectInputHasError?"invalid-input":""} onChange={subjectChangeHandler} onBlur={subjectBlurHandler} value={enterdSubject} type="text" placeholder="SUBJECT"></input>
                                {subjectInputHasError && <p className={"error-text"}>Please enter a valid subject.</p>}
                            </div>
                            <div className={classes["form-input"]}>
                                <textarea ref={content} className={messageInputHasError?"invalid-input":""} onChange={messageChangeHandler} onBlur={messageBlurHandler} value={enterdMessage} placeholder="MESSAGE"></textarea>
                                {messageInputHasError && <p className={"error-text"}>Please enter a valid message.</p>}

                            </div>
                            <div className={classes.submit}>
                                <button className={formValidity?"":"disabled-button"} disabled={!formValidity} type="submit">SEND IT</button>
                            </div>
                        </form>
                    </div>
                </div>
            </div>
            {loading && <Spinner />}
            {error && <Modal error={true} onClose={closeModal}>{error}</Modal>}
            {responseMessage && <Modal error={false} onClose={closeModal}>{responseMessage}</Modal>}
        </Fragment>
    )
}

export default ContactMe;