import { Fragment } from "react"
import ReactDOM from "react-dom"

import classes from "./modal.module.css"


const ModalOverLay = (props) => {
    return (
        <Fragment>
        <div className={classes["overlay-container"]}>
            <div className={classes.overlay} onClick={props.onClose}></div>
            <div className={classes["modal-container"]}>
                <div className={`${classes["modal-content"]} ${props.error?classes["error"]:classes["success"]}`}>
                    {props.children}
                </div>
                <div className={classes["modal-controls"]}>
                    {
                       props.onConfirm && <button className={classes["modal-button"]} onClick={props?.onConfirm}>Confirm</button>
                    }
                    <button className={classes["modal-button"]} onClick={props.onClose}>Close</button>
                </div>
            </div>
        </div>
        </Fragment>
    )
} 

const portalElement = document.getElementById("overlays")

const Modal = (props) => {
    return (
        ReactDOM.createPortal(<ModalOverLay onClose={props.onClose} error={props.error} onConfirm={props?.onConfirm}>{props.children}</ModalOverLay>, portalElement)
    )
}

export default Modal;